.RestHeaderCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    height: 100vh;
    font-size: 3vw;
    background: #EBEBEB;
}

.Z1{
    z-index: 0;
    opacity: 1;
}

.Z2{
    z-index: 1;
    opacity: 0;
}

.Z3{
    z-index: 2;
    opacity: 0;
}
.Z4{
    z-index: 3;
    opacity: 0;
}

.Z1, .Z2, .Z3, .Z4{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    transition: opacity 2s ease-in-out;
}

.Z--Active{
    opacity: 1;
}

.RestNavCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    padding: 1.5vw 0px;
    font-size: 1em;
    height: 5vh;
    z-index: 5;
}

.RestNavButtonCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 50vw;
    font-size: 0.4em;
    font-family: 'Raleway', sans-serif;
    color: white;
}

.RestNavButtonCont img{
    width: 10vw;
}

.RestNavButtonCont a{
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.RestNavButtonCont a:hover{
    border-bottom: 1px #A48F7A solid;
    color: #A48F7A;
}

.RestNavButtonContTwo{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 20vw;
}

.RestMenuButton{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 3vw;
    height: 3vw;
    border: 1px white solid;
    border-radius: 40px;
    font-size: 0.4em;
    font-family: 'Raleway', sans-serif;
    color: white;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

.RestMenuButton:hover{
    background: #A48F7A;
}

.RestHeaderSliderCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    height: 90vw;
    width: 100vw;
    z-index: 3;
    font-size: 1em;
}

.RestHeaderTitleCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    font-size: 1em;
    font-family: 'Cormorant', sans-serif;
    color: white;
    width: 80vw;
    height: 30vh;
}

.RestHeaderSliderCont img{
    cursor: pointer;
    width: 2.5vw;
    transition: 0.3s ease-in-out;
}

.RestHeaderSliderCont img:hover{
    transform: scale(1.3);
}

.RestHeaderTitleOne{
    width: 50vw;
}

.RestHeaderBron{
    display: flex;
    width: 15vw;
    align-items: center;
    justify-content: space-between;
    font-size: 0.4em;
    font-family: 'Raleway', sans-serif;
    color: white;
    cursor: pointer;
    transition: 0.3s ease-in-out;    
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.RestArrow{
    background: url('../../StaticElement/Image/TheView/Arrow.png');
    background-repeat: no-repeat;
    background-position-x: left;
    background-size: contain;
    background-position-y: 60%;
    width: 5vw;
    height: 3vw;
    transition: 0.3s ease-in-out;
}

.RestHeaderBron:hover .RestArrow{
    background: url('../../StaticElement/Image/TheView/ArrowHover.png');
    background-position-y: 70%;
    background-position-x: left;
    background-repeat: no-repeat;
    background-size: contain;
}

.RestHeaderBron:hover{
    color: #A48F7A;
    width: 17vw;
}

.RestHeaderFooterCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 97vw;
    padding: 1.5vw;
}

.RestHeaderFooterTrash{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.4em;
    font-family: 'Raleway', sans-serif;
    color: white;
    cursor: pointer;
    width: 7vw;
    height: 0.7vw;
    border: 1px white solid;
    border-radius: 40px;
    padding: 1.5vw;
    z-index: 5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(2px);
    transition: 0.5s ease-in-out;
}

.RestHeaderFooterTrash .RestTrash{
    position: relative;
    right: -0.8vw;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 0.85vw;
    border: 1px white solid;
    transition: 0.5s ease-in-out;
}

.RestHeaderFooterTrash:hover .RestTrash{
    right: 5.5vw;
}

.RestHeaderFooterTrash:hover{
    background: #A48F7A;
}

.RestTrashTitle{
    position: relative;
    left: 0.3vw;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s ease-in-out;
}

.RestHeaderFooterTrash:hover .RestTrashTitle{
    left: 3.8vw;
}

.CubeButtonCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 20%;
    z-index: 5;
}

.CubeButton{
    width: 4vw;
    height: 0.3vw;
    background: rgba(255, 255, 255, 0.5);
    transition: 1s ease-in-out;  
    cursor: pointer;
}

.CubeButton--Active{
    background: rgba(255, 255, 255, 1);
}

.RestInvBox{
    width: 9vw;
    height: 0.5vw;
}

.RestTrashEl{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40vw;
    overflow: scroll;
    font-size: 1em;
    height: 50%;
}
.RestTrashEl::-webkit-scrollbar{
    display: flex;
    height: 100%;
    background: transparent;
    cursor: pointer;
}

.RestTrashEl::-webkit-scrollbar-thumb{
    height: auto;
    border-radius: 10px;
    background: rgb(214, 214, 214);
}

.RestTrashModal{
    width: 50vw;
}

.RestTrashModal form{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    height: 40%;
    width: 40vw;
    font-size: 1em;
}

.RestTrashModal form input{
    width: 90%;
}

.RestTrashCardCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: 1em;
    width: 38vw;
    padding-bottom: 0.5vw;
    border-bottom: 1px #8D8D8D solid;
}

.RestTrashCardTitle{
    width: 60%;
    font-size: 0.4em;
    font-family: 'Cormorant', sans-serif;
}

.RestTrashCardCount{
    display: flex;
    align-items: center;
    font-size: 0.5em;
    font-family: 'Cormorant', sans-serif;
}

.RestTrashCardCount div{
    padding: 0px 1vw;
}
