.HomeCont{
    display: flex;
    width: 100vw;
    font-size: 5vw;
}

.StartWindow{
    display: flex;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
}

table, td{
    border-top: 0px white solid;
    border-right: 1px rgba(255, 255, 255, 0.4) solid;
    border-bottom: 1px rgba(255, 255, 255, 0.4) solid;
    border-collapse: collapse;
}

.GridLineOne{
    height: 10vh;
    border: 0px;
}

.GridLineTwo{
    height: 90vh;
}

.td--noRightLine{
    border-right: 0px;
}

.td--noTopLine{
    border-top: 0px;
}

.td--noTopLine .select{
    margin: 0px 2.5vw;
}

.td--noBottomLine{
    border-bottom: 0px;
}

.td--extreme{
    width: 3vw;
}

.Logo1 svg{
    position: relative;
    left: calc(50% - 5vw);
    top: -0.3vw;
    width: 10vw;
}

.Menu{
    padding: 0px;
    margin: 0px;
    width: 1vw;
}

.MenuButtonText{
    position: absolute;
    top: 2.1vw;
    right: 5.2vw;
    font-size: 0.3em;
    color: white;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
}

.Menu svg{
    position: relative;
    top: -0.8vw;
    width: 8vw;
    padding: 0px 1vw;
    margin: 0px;
    cursor: pointer;
}

.SocialCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 25vw;
}

.SocialCont svg{
    transition: 0.3s ease-in-out;
}

.SocialCont svg:hover{
    transform: scale(1.5);
}

.HomeLogo{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50%;
    margin-bottom: 10vw;
}


.HomeLogo a{
    position: absolute;
    bottom: 2vw;
    left: calc(50%-75px);
    width: 150px;
}

.Z1{
    z-index: 0;
    opacity: 1;
}

.Z2{
    z-index: 1;
    opacity: 0;
}

.Z3{
    z-index: 2;
    opacity: 0;
}
.Z4{
    z-index: 3;
    opacity: 0;
}

.Z1, .Z2, .Z3, .Z4{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    transition: opacity 2s ease-in-out;
}

.Z--Active{
    opacity: 1;
}

.CircleButtonCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 20vw;
}

.CircleButton{
    border-radius: 50%;
    width: 1.3vw;
    height: 1.3vw;
    background-color: white;
    transition: 1s ease-in-out;  
    outline-offset: 3px;  
    outline: 2px solid;
    outline-color: rgba(178, 68, 30, 0);
    cursor: pointer;
}

.CircleButton--Active{
    outline-color: rgba(178, 68, 30, 1);
}
