.RestPhotoCardCont{
    font-size: 1em;
    z-index: 5;
}

.RestPhotoCardCont:hover .RestPhotoCardPhoto{
    filter: brightness(0.3);
}

.RestPhotoCardCont:hover .RestPhotoCardTextCont{
    opacity: 1;
}

.RestPhotoCardPhoto{
    transition: 0.5s ease-in-out;
}

.RestPhotoCardTextCont{
    position: relative;
    top: -100%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1em;
    text-align: center;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.RestPhotoCardTextTitle{
    font-family: 'Cormorant', sans-serif;
    font-size: 0.7em;
    font-family: 400;
    width: 60%;
    color: white;
}

.RestPhotoCardText{
    font-family: 'Cormorant', sans-serif;
    font-size: 0.5em;
    color: white;
}