.MenuCont{
    display: flex;
    position: fixed;
    top: 0px;
    right: 0px;
    height: 100vh;
    width: 30vw;
    background: rgba(0, 0, 0, 0.8);
    box-shadow: 0px 4px 9px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(5px);
    z-index: 10;
    font-size: 3vw;
    transition: 1s ease-in-out;
    transform: translateX(31vw);
}

.Menu--Open{
    transform: translateX(0px);    
}

.MenuContInside{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: top;
    padding: 3vw;
    width: 100%;
}

.MenuTitle{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
    width: 100%;
    font-size: 0.8em;
    border-bottom: 0.6px rgba(255, 255, 255, 0.5) solid;
    padding-bottom: 3px;
}

.MenuTitle svg{
    width: 4vw;
}

.MenuButton{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    font-size: 0.7em;
    height: 60%;
    width: 50%;
    margin-top: 3vw;
}

.MenuButton a{
    font-size: 0.7em;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
    cursor: pointer;
    border-bottom: 2px rgba(178, 68, 30, 0) solid;
    transition: 0.5s ease-in-out;
}

.MenuButton a:hover{
    color: #B2441E;
    border-color: #B2441E;
}