.FooterCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 3vw;
    font-size: 3vw;
    height: 17vw;
}

.FooterMenu{
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    color: black;
    font-size: 0.4em;
    font-family: 'Raleway', serif;
    height: 100%;
}

.FooterMenu div{
    color: black;
    font-size: 2.5em;
    font-family: 'Cormorant', serif;
    font-weight: 600;
    margin-bottom: 1vw;
}

.FooterMenu a{
    cursor: pointer;
}

.FooterLogo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    color: black;
    font-size: 0.4em;
    font-family: 'Raleway', serif;
    height: 100%;
}

.FooterLogo img{
    width: 15vw;
}