.GalleryCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: auto;
    font-size: 3vw;
    padding-top: 1.5vw;
}

.GalleryTitleOne{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Raleway', sans-serif;
    font-size: 0.5em;
}
.GalleryTitleTwo{
    font-family: 'Cormorant', serif;
    font-weight: 600;
    font-size: 1em;
    padding: 2vw;
}

.GalleryGridCont{
    display: grid;
    width: 100%;
    height: 20vw;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    z-index: 1;
}

.GalleryGridContInsideOne{
    display: subgrid;
    width: 100%;
    grid-template-columns: 1;
    grid-template-rows: repeat(2, 1fr);
}

.GalleryGridContInsideTwo{
    display: subgrid;
    width: 100%;
    grid-template-columns: 1;
    grid-template-rows: repeat(3, 1fr);
}

.GalleryGridContInsideThree{
    display: subgrid;
    width: 100%;
    grid-template-columns: 1;
    grid-template-rows: repeat(2, 1fr);
}

.PhotoCardCont{
    font-size: 1em;
}

.PhotoCardCont:hover .PhotoCardPhoto{
    filter: brightness(0.3);
}

.PhotoCardCont:hover .PhotoCardTextCont{
    opacity: 1;
}

.PhotoCardPhoto{
    position: relative;
    top: -100%;
    z-index: -1;
    transition: 0.5s ease-in-out;
}

.PhotoCardTextCont{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1em;
    text-align: center;
    opacity: 0;
    transition: 0.5s ease-in-out;
}

.PhotoCardTextTitle{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Cormorant', sans-serif;
    font-size: 0.7em;
    font-family: 400;
    width: 60%;
    color: white;
}

.PhotoCardText{
    font-family: 'Raleway', sans-serif;
    font-size: 0.5em;
    color: white;
}

.GalleryButton{
    background: transparent;
    font-family: 'Raleway', sans-serif;
    border: 1px #B2441E solid;
    color: #161616;
    margin-top: 63vw;
    padding: 1vw 3vw;
    font-size: 0.5em;
}
