.EstablishmentsCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    height: 100vh;
    font-size: 3vw;
    padding-top: 1.5vw;
}

.EstablishmentsTitleOne{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Raleway', sans-serif;
    font-size: 0.5em;
}
.EstablishmentsTitleTwo{
    font-family: 'Cormorant', serif;
    font-weight: 600;
    font-size: 1em;
}

.EstablishmentsList{
    display: flex;
    flex-direction: row;
    align-items: top;
    justify-content: space-between;
    font-size: 1em;
    width: 95vw;
    height: 36vw;
}

.EstablishmentsCardCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: calc(90vw/4);
    font-size: 1em;
    height: 100%;
}

.EstablishmentsCardPhotoCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 18vw;
    height: 15vw;
}

.PhotoRoom{
    position: relative;
    top: 3vw;
    width: 16.5vw;
    transition: 0.5s ease-in-out;
}

.PhotoRoom:hover{
    transform: scale(1.1);
    cursor: pointer;
}

.EstablishmentsLogo{
    position: relative;
    bottom: 3vw;
    width: 7vw;
}

.EstablishmentsCardTitle{
    font-size: 1em;
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Cormorant', serif;
    font-weight: 400;
    width: 100%;
    text-align: center;
}

.EstablishmentsCardInfoCont{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 1em;
    width: 100%;
    height: 10vw;
}

.EstablishmentsCardInfo{
    font-family: 'Raleway', sans-serif;
    font-size: 0.35em;
    padding: 0.5vw 0px;
}

.EstablishmentsCardInfo a{
    color: black;
}