.RestContactCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 100vw;
    padding: 2vw;
    font-size: 3vw;
    margin: 0px;
}

.RestContact{
    display: flex;
    flex-direction: column;
    align-items: left;
    font-size: 1em;
}

.RestContactTitle{
    font-size: 0.6em;
    font-family: 'Cormorant', sans-serif;
    font-weight: 800;
    margin-top: 2vw;
}

.RestContactText{
    font-size: 0.4em;
    font-family: 'Cormorant', sans-serif;
    font-weight: 500;
    width: 17vw;
}

.RestContactBron{
    font-family: 'Cormorant', sans-serif;
    font-style: italic;
    font-weight: 600;
    font-size: 0.5em;
    border-bottom: 1px black solid;
    cursor: pointer;
    padding: 0.3vw 0px;
    position: relative;
    top: 2vw;
    width: 14vw;
}

.RestContactMap{
    width: 30vw;
    height: 30vw;
}