.RestMenuCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 3vw;
    padding: 2vw;
    background: white;
    height: 40vw;
}

.MenuPhoto{
    width: 30vw;
}

.RestMenuBlockTwo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 1em;
    text-align: center;
    font-family: 'Cormorant', sans-serif;
    width: 40vw;
    height: 100%;
}

.RestMenuBlockTwoTitle{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.75em;
    color: #9B8876;
}

.RestMenuBlockTwoBody{
    width: 75%;
    font-size: 0.6em;
    font-weight: 500;
}

.RestMenuShowButton{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 3vw;
    height: 3vw;
    border: 1px black solid;
    border-radius: 40px;
    font-size: 0.35em;
    font-family: 'Raleway', sans-serif;
    color: black;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

.RestMenuShowButton:hover{
    background: #A48F7A;
    color: white;
}