.RestTour{
    display: inline-flex;
    width: 200vw;
    padding: 0px;
    margin: 0px;
}
.RestTourCont, .RestSchemaCont{
    position: relative;
    display: flex;
    flex-direction: row;
    left: 0px;
    align-items: center;
    justify-content: center;
    font-size: 3vw;
    background: white;
    padding: 5vw 6vw;
    width: 100vw;
    margin: 0px;
    transition: left 0.5s ease-in-out;
}


.RestTour--Active{
    left: -100vw;
}
.RestSchema--Active{
    left: -100vw;
}



.TourPhoto{
    width: 30vw;
}

.RestTourBlockTwo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 1em;
    text-align: center;
    font-family: 'Cormorant', sans-serif;
    width: 40vw;
    height: 35vw;
}

.RestTourBlockTwoTitle{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.75em;
    color: #9B8876;
}

.RestTourBlockTwoBody{
    width: 65%;
    font-size: 0.6em;
    font-weight: 500;
}
