.RestGalleryCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3vw;
    padding: 4vw;
}

.RestGalleryTitle{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Cormorant', sans-serif;
    font-size: 1.75em;
    color: #9B8876;
}

.RestGalleryNavCont{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 30vw;
    font-family: 'Raleway', sans-serif;
    font-size: 0.4em;
    margin: 3vw;
}

.RestGalleryNavEl{
    cursor: pointer;
    transition: 0.5s ease-in-out;
    padding: 0.5vw;
}

.RestGalleryNavEl--Active{
    border-bottom: 1.5px black solid;
}