.RestCommentCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 3vw;
    padding: 4vw;
    background-color: #EBEBEB;
}

.RestCommentTitle{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Cormorant', sans-serif;
    font-size: 1.75em;
    color: #9B8876;
}

.RestCommentCardTitle{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: 'Cormorant', sans-serif;
    font-size: 0.7em;
    color: black;
    padding: 1vw 0px;
    width: 100%;
    font-weight: 700;
    border-bottom:1px black solid;
}

.RestCommentCardBody{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-size: 0.25em;
    padding: 1vw;
    font-family: 'Raleway', sans-serif;
    text-align: left;
    font-weight: 500;
    height: 69%;
}

.RestCommentCardBodyRatingCont{
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: space-between;
    text-align: left;
    width: 100%;
}

.RestCommentCardRatingElCont{
    display: flex;
    flex-direction: row;
    align-items: bottom;
    justify-content: space-between;
    width: 100%;
}

.RestCommentCardRatingStar{
    width: 30%;
}
.RestCommentCardTitle svg{
    margin: 0px 1vw;
}


.Restcard {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    padding: 0px;
    width: 20vw;
    height: 22vw;
    min-width: 100px;
    z-index: 0;
    transition: 500ms;
    font-size: 3vw;
    opacity: 0;
    border: 1px black solid;
}
  
.card--active {
    opacity: 1;
    transform: scale(1);
    z-index: 1;
}

.card--left {
    opacity: 1;
    transform: translateX(-100%) scale(0.9);
    transition: 500ms;
    z-index: 0;
}

.card--right {
    opacity: 1;
    transform: translateX(100%) scale(0.9);
    transition: 500ms;    
    z-index: 0;
}

.RestCommentModalCont{
    display: none;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background: transparent;
    backdrop-filter: blur(9px);
    z-index: 10;
    font-size: 1em;
}
.RestCommentModalCont--Active{
    display: flex;
}

.RestCommentModal{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 30vw;
    height: 80vh;
    background: #EBEBEB;
    box-shadow: 0px 4px 35px rgba(0, 0, 0, 0.25);
    font-size: 1em;
    padding: 2vw;
}

.RestCommentTitleModal{
    font-size: 0.7em;
    font-weight: 700;
}

.RestCommentDiscModal{
    display: flex;
    text-align: center;
    font-size: 0.35em;
    font-family: 'Cormorant', sans-serif;
    font-weight: 600;
}

.RestCommentModal form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    font-size: 1em;
}

.RestCommentModal form input{
    font-size: 0.4em;
    font-family: 'Cormorant', sans-serif;
    background: #FAFAFA;
    box-shadow: 1px 2px 9px rgba(0, 0, 0, 0.25);
    border: none;
    width: 25vw;
    height: 2vw;
}

.RestCommentModal form textarea{
    font-size: 0.4em;
    font-family: 'Cormorant', sans-serif;
    background: #FAFAFA;
    box-shadow: 1px 2px 9px rgba(0, 0, 0, 0.25);
    border: none;
    width: 25vw;
    resize: none;
}

.RestCommentModal form input:focus{
    border: none;
    outline: none;
}

.RestCommentModal form textarea:focus{
    border: none;
    outline: none;
}

.RestCommentCardRatingElContModal{
    width: 25vw;
    font-size: 1em;
}

.RestCommentCardRatingStarModal{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 16.5%;
}

.RestCommentCardRatingElContModal div{
    font-family:'Cormorant', sans-serif;
    font-size: 0.4em;
    color: rgba(0, 0, 0, 0.5);

}

.RestRatingStar1, .RestRatingStar2, .RestRatingStar3, .RestRatingStar4, .RestRatingStar5{
    background: url('../../StaticElement/Image/StarGray.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 13px;
    height: 13px;
    cursor: pointer;
}

.RestRatingStar--Active{
    background: url('../../StaticElement/Image/StarGold.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 13px;
    height: 13px;
}