.EventsCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    font-size: 3vw;
    padding-top: 1.5vw;
}

.EventsTitleOne{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Raleway', sans-serif;
    font-size: 0.5em;
}
.EventsTitleTwo{
    font-family: 'Cormorant', serif;
    font-weight: 600;
    font-size: 1em;
    padding: 2vw;
}

.EventsList{
    display: flex;
    flex-direction: row;
    align-items: top;
    justify-content: space-between;
    font-size: 1em;
    width: 95vw;
    height: 36vw;
}


.card {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    padding: 0px;
    width: 20vw;
    height: 30vw;
    min-width: 100px;
    z-index: 0;
    transition: 500ms;
    font-size: 3vw;
    opacity: 0;
  }
  
  .card--active {
    opacity: 1;
    transform: scale(1);
    z-index: 1;
  }
  
  .card--left {
    opacity: 1;
    transform: translateX(-100%) scale(0.9);
    transition: 500ms;
    z-index: 0;
  }
  
  .card--right {
    opacity: 1;
    transform: translateX(100%) scale(0.9);
    transition: 500ms;    
    z-index: 0;
  }


  .EventsCardPhoto{
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% + 2px);
    height: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-size: 1em;
    transition: 0.5s ease-in-out;
    z-index: 0;
  }

  .EventsCardButton{
    position: absolute;
    top: calc(7.5vw - 1vw - 0.55em/2);
    left: calc(50% - 7vw);
    background: #B2441E;
    padding: 1vw 3vw;
    font-family: 'Raleway', sans-serif;
    font-size: 0.55em;    
    font-weight: 200;
    color: white;
    transition: 0.5s ease-in-out;
    z-index: 1;
    opacity: 0;
  }

  .card:hover .EventsCardPhoto{
    filter: brightness(0.3);
  }
  .card:hover .EventsCardButton{
    opacity: 1;
  }

  .EventsCardBody{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    border: 1px rgb(188, 188, 188) solid;
    border-top: 0px white solid;
    width: 100%;
    height: 50%;
    z-index: 1;
    background: white;
  }

  .EventsCardTextCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    font-size: 1em;
    padding-top: 1.5vw;
  }

  .EventsCardText{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Raleway', sans-serif;
    font-size: 0.4em;    
    font-weight: 300;
  }

  .EventsCardTextContTwo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 80%;
    height: 100%;
    font-size: 1em;
    overflow: hidden;
    padding: 1.5vw 0px;
    text-align: left;
  }

  .EventsCardTextContTwoTitle{
    font-family: 'Cormorant', sans-serif;
    font-weight: 600;
    font-size: 0.5em;    
  }

  .EventsCardTextContTwoText{
    font-family: 'Raleway', sans-serif;
    font-size: 0.35em;    
    font-weight: 200;  
    margin-top: 0.5vw;  
  }