.CommentsCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    font-size: 3vw;
    padding-top: 3vw;
}

.CommentsTitleOne{
    border-top: 1px #B2441E solid ;
    border-bottom: 1px #B2441E solid ;
    font-family: 'Raleway', sans-serif;
    font-size: 0.5em;
}
.CommentsTitleTwo{
    font-family: 'Cormorant', serif;
    font-weight: 600;
    font-size: 1em;
    padding-top: 2vw;
}

.CommentCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25vw;
    border: 1px #818181 solid;
    padding: 2vw;
    font-size: 1em;
    margin: 0px 2vw;
    transition: 0.5s ease-in-out;
}

.CommentCont--Active{
    padding: 3vw;
}

.CommentHeaderCont{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    font-size: 1em;
}

.CommentHeaderContInside{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 1em;
}

.CommentHeaderTextOne{
    color: black;
    font-size: 0.6em;
    font-family: 'Cormorant', serif;    
}

.CommentHeaderTextTwo{
    color: #B2441E;
    font-size: 0.4em;
    font-family: 'Josefin Sans', serif;
}

.CommentText{
    color: black;
    font-size: 0.5em;
    font-family: 'Cormorant', serif;
    width: 90%;
    margin: 0px;
    padding: 0px;
}