.select {
	margin: 0 auto;
	position: relative;
	width: 50px;
	margin: 0;
    font-family: 'Josefin Sans', sans-serif;
	z-index: 6;
}
.select.is-active .select__body {
	display: block;}

.select__header {
	border: transparent;
	cursor: pointer;
	display: flex;
    color: white;
    z-index: 1;
    background: url('../../Image/coolicon.png') no-repeat right;
}
.select__current {
	font-size: 18px;
	line-height: 24px;
    z-index: 0;
}

.select__body {
	border: transparent;
	border-top: 0;
	display: none;
	left: 0%;
	position: absolute;
	right: 0;
	top: 100%;
    color: white;
	width: 100%;
	text-align: left;
}
.select__item {
	cursor: pointer;
	font-size: 16px;
	line-height: 28px;
	padding: 4px 8px;
}

.select__item:hover {
	background-color: #3c3c3c;
}