  .CaruselCont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    font-size: 1.3vw;
  }
  
  .Carousel {
    width: 100%;
    height: 30vw;
    position: relative;
    font-family: LatoLight;
    font-size: 1vw;
  }
  
  .Arrows{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 25vw;
    padding-bottom: 2vw;
  }


  @media (max-width:960px) {
    .Arrows{
      width: 50vw;
    }
    .CaruselCont{
      height: 80vw;
    }
    .CaruselCont{
      font-size: 2vw;
    }
  }

  @media (max-width: 500px) {
    .Arrows{
      width: 80vw;
    }
    .CaruselCont{
      height: 100vw;
      font-size: 5vw;
      margin-top: 0vw;
    }
    .card{
      width: 25vw;
    }
  }